.characters__item {
  width: 240px;

  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media (max-width: 540px) {
    width: 312px;
  }
  &:hover {
    transform: scale(1.035);
  }
}

.character__wrap {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  height: 100%;
}

.characters__image {
  height: 168px;
  width: 240;
  object-fit: cover;
  border-radius: 4px 4px 0 0;
  @media (max-width: 540px) {
    height: 232px;
  }
}

.character__info {
  padding: 12px 16px;
  /* flex-grow: 1; */
  /* height: 100%; */
}

.character__name {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
}

.character__specie {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.25px;
  color: rgba(0, 0, 0, 0.6);
}
